
import Vue from 'vue'
export default {
	onLaunch: function() {
	    uni.getSystemInfo({
	    	success: function(e) {








	    

	    		Vue.prototype.StatusBar = e.statusBarHeight;
	    		let capsule = wx.getMenuButtonBoundingClientRect();
	    		if (capsule) {
	    			Vue.prototype.Custom = capsule;
	    			// Vue.prototype.capsuleSafe = uni.upx2px(750) - capsule.left + uni.upx2px(750) - capsule.right;
